import React, { Component } from 'react';
import { ImageView } from 'zzc-design-mobile';
import './index.scss';
import '../../../style/style.scss';

export default class App extends Component {
    constructor( props ) {
        super( props );
        this.state = {
            visible: false,
            imageList: [
                'https://imgcdn50.zuzuche.com/world/survey/view/27/a71ccddf81312728a9b2766dcff3eac9_200x200.jpg',
                'https://imgcdn50.zuzuche.com/world/survey/view/27/a71ccddf81312728a9b2766dcff3eac9_200x200.jpg',
                'https://imgcdn50.zuzuche.com/world/survey/view/27/a71ccddf81312728a9b2766dcff3eac9_200x200.jpg',
                'https://imgcdn50.zuzuche.com/world/survey/view/27/a71ccddf81312728a9b2766dcff3eac9_200x200.jpg',
                'https://imgcdn50.zuzuche.com/world/survey/view/27/a71ccddf81312728a9b2766dcff3eac9_200x200.jpg',
                'https://imgcdn50.zuzuche.com/world/survey/view/27/a71ccddf81312728a9b2766dcff3eac9_200x200.jpg'
                // 'data:image/jpeg;base64,',
                // 'data:image/jpeg;base64,',
                // 'data:image/jpeg;base64,',
                // 'data:image/jpeg;base64,',
                // 'data:image/jpeg;base64,',
                // 'data:image/jpeg;base64,'
            ]
        };
    }

    openImageView( key ) {
        this.setState( {
            visible: true,
            index: key
        } );
    }

    closeImageView( key ) {
        console.log( key );
        this.setState( {
            visible: false
        } );
    }

    render() {
        return (
            <div className='zzc-demo'>
                <div className='zzc-demo-header'>
                    <h1 className='zzc-demo-title'>ImageView 图片放大框</h1>
                </div>
                <div className='zzc-demo-body'>
                    <ul className='image-list'>
                        {
                            this.state.imageList.map( ( item, key ) => ( <li onClick={() => { this.openImageView( key ); }} key={key}><img src={item} alt='' /></li> ) )
                        }
                    </ul>
                    <ImageView
                        defaultIndex={this.state.index}
                        onClose={( index ) => { this.closeImageView( index ); }}
                        imageList={this.state.imageList}
                        visible={this.state.visible}
                        onChange={( index ) => { console.log( index ); }}
                    />
                </div>
            </div>
        );
    }
}


